import React, { Component } from "react";
import './tuSol.css';

export default class TUStage extends Component {
  render() {
    return (      
      <div className="md-grid--no-spaces tustage">
        <div id="solsys" className="view-3D zoom-large stage">
          <div id="universe" className="scale-stretched">
            <div id="galaxy">
              <div id="solar-system" className="earth">
                <div id="mercury" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Mercury</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="venus" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Venus</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="earth" className="orbit">
                  <div className="pos">
                    <div className="orbit">
                      <div className="pos">
                        <div className="moon" />
                      </div>
                    </div>
                    <div className="planet">
                      <dl className="infos">
                        <dt>Earth</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="mars" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Mars</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="jupiter" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Jupiter</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="saturn" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <div className="ring" />
                      <dl className="infos">
                        <dt>Saturn</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="uranus" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Uranus</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="neptune" className="orbit">
                  <div className="pos">
                    <div className="planet">
                      <dl className="infos">
                        <dt>Neptune</dt>
                        <dd>
                          <span />
                        </dd>
                      </dl>
                    </div>
                  </div>
                </div>
                <div id="sun">
                  <dl className="infos">
                    <dt>Sun</dt>
                    <dd>
                      <span />
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



